<template>
	<div class="infoManager" id="infoManager">
		<b-card class="mb-2" v-for="(exam,index) in examsData" :key="exam.examId">
			<b-link v-b-toggle="'collapse' + index" size="sm" @click="collapseS(index)"><span :class="classSwitch[0][index]">&nbsp;</span></b-link>{{exam.examName}}
			<div id="card-right-box" class="card-right-box">
				<b-btn size="sm" @click="showAchievementModal(exam.examName)">出成绩</b-btn>
				<span class="line">&nbsp;|&nbsp;</span>
				<b-btn size="sm" class="fa fa-cog" title="修改"></b-btn>
				<b-btn size="sm" class="fa fa-bitbucket" title="删除"></b-btn>
			</div>
			<b-collapse :id="'collapse'+index" class="mt-2">
				<b-card class="mb-3">
					<ul>
						<li><span class="fa fa-file-text">&nbsp;</span><strong>语文</strong><span class="line">&nbsp;|&nbsp;</span>扫描：
							<div class="progressBox">
								<b-progress v-model="counter" :precision="1" show-progress animated></b-progress>
							</div><span class="line">&nbsp;|&nbsp;</span>评卷：
							<div class="progressBox">
								<b-progress v-model="counter" :precision="1" show-progress animated></b-progress>
							</div><span class="line">&nbsp;|&nbsp;</span>

							<div class="popoverBox">
								<div class="popover1" v-for="trigger in triggers">
									<b-popover :popover-style="popoverStyle" placement="bottom" :triggers="trigger" title="待处理" content="
					<a href='JavaScript:alert(`收集题卡信息`)'>收集题卡信息</a><br/>
					<a href='JavaScript:alert(`录入标答与分值`)'>录入标答与分值</a><br/>
					<a href='JavaScript:alert(`评卷方案与任务分配`)'>评卷方案与任务分配</a><br/>
					<a href='JavaScript:alert(`开始试评答卷`)'>开始试评答卷</a><br/>
					<a href='JavaScript:alert(`开启正式评卷`)'>开启正式评卷(重评)</a><br/>
					<a href='JavaScript:alert(`成绩检查上报`)'>成绩检查上报</a>">
										<a @click="triggersToString(trigger)" href="javascript:void(0)"><span class="fa fa-star">&nbsp;</span>待处理
											<b-badge variant="danger" class="badge-1">5</b-badge>
										</a>
									</b-popover>
								</div>
							</div>
						</li>
					</ul>
				</b-card>
			</b-collapse>
		</b-card>
		<!--出成绩模态框-->
		<b-modal id="achievement" title="出成绩" size='lg' :fade="motalFade" :close-on-backdrop="closeOnBackdrop" close-title="关闭模态框" @hidden="Modalhidden">
			<div slot="modal-title">{{modalPageTabArr[modalCurrentPage].pageTitle}}</div>
			<div :class="{hide:modalPageTabArr[0].displaySn}">
				<b-table striped hover :items="items" :fields="fields" :current-page="currentPage" :per-page="perPage" :filter="filter" :class="{table1:true}">
					<template slot="name" scope="item">
						{{item.value}}
					</template>
					<template slot="isActive" scope="item">
						{{item.value?'有效':'无效'}}
					</template>
					<template slot="actions" scope="item">
						<b-btn size="sm" @click="details(item.item),showAlert(item.item),$root.$emit('show::modal','modal1')">修改</b-btn>
						<b-btn size="sm" @click="details(item.item)">删除</b-btn>
					</template>
				</b-table>
			</div>
			<div :class="{hide:modalPageTabArr[1].displaySn}">22222222222222222</div>
			<div :class="{hide:modalPageTabArr[2].displaySn}">3333333333</div>
			<div slot="modal-footer" class="modal-footer-btn-center">
				<b-button variant='success' size='md' @click="modalPageTab(modalCurrentPage--)">上一步</b-button>
				<b-button variant='success' size='md' @click="modalPageTab(modalCurrentPage++)" v-if="modalCurrentPage!==modalPageTabArr.length-1">下一步</b-button>
				<b-button variant='success' size='md' v-if="modalCurrentPage==modalPageTabArr.length-1" @click="submitExam">提　交</b-button>
			</div>
		</b-modal>
	</div>
</template>

<script>
	import { httpSync } from '../module/servers-tools';

	export default({
		name: "infoManager",
		data() {
			return {
				//class
				isPlusSquare: true,
				isMinusSquare: false,
				classSwitch: [[], {
					classItem: ""
				}],
				//进度条
				counter: 83,
				triggers: [['click', 'focus']],
				popoverStyle: {
					"z-index": "1060"
				},
				//考试数据,接口数据
				examsData: [],
				//modal
				motalFade: true,
				closeOnBackdrop: false,
				//modalPageTabArr 模态框页面翻页
				modalPageTabArr: [{
					pageId: 1,
					displaySn: 0,
					pageTitle: ""
				}, {
					pageId: 2,
					displaySn: 1,
					pageTitle: ""
				}, {
					pageId: 3,
					displaySn: 1,
					pageTitle: ""
				}],
				modalCurrentPage: 0,
				//出成绩模态框表格数据
				items: [],
				currentPage: 1,
				perPage: 10,
				filter: null,
				fields: {
					name: {
						label: '姓名',
						sortable: true
					},
					age: {
						label: '年龄',
						sortable: true
					},
					isActive: {
						label: '是否有效'
					},
					actions: {
						label: '操作'
					}
				}
			}
		},
		//渲染之前
		beforeCreate() {},
		//完成了 data 数据的初始化,初始化数据
		async created() {
			this.examsData = await this.getExamOriginData();
			this.items = await this.getExamItemsOrigin();
			this.setClassSwitch();
		},
		methods: {
			/*获取考试数据*/
			getExamOriginData() {
				return [{
						"id": 1,
						"chargeId": ["1477712060454", "1477712060453", "1477712060452", "1477712060451"],
						"examDate": ["2017-04-26"],
						"examId": 0,
						"examName": "深圳市罗湖区翠园中学期中模拟考试"
					},
					{
						"id": 2,
						"chargeId": ["1477712060454", "1477712060453", "1477712060452", "1477712060451"],
						"examDate": ["2017-04-26"],
						"examId": 0,
						"examName": "深圳市罗湖区翠园中学单元(一)测试考试"
					},
					{
						"id": 3,
						"chargeId": ["1477712060454", "1477712060453", "1477712060452", "1477712060451"],
						"examDate": ["2017-04-26"],
						"examId": 0,
						"examName": "深圳市福田区思源小学单元(一)测试考试"
					},
					{
						"id": 3,
						"chargeId": ["1477712060454", "1477712060453", "1477712060452", "1477712060451"],
						"examDate": ["2017-04-26"],
						"examId": 0,
						"examName": "深圳市南山区清源小学期末模拟考试"
					},
					{
						"id": 3,
						"chargeId": ["1477712060454", "1477712060453", "1477712060452", "1477712060451"],
						"examDate": ["2017-04-26"],
						"examId": 0,
						"examName": "深圳市光明新区区智慧小学期中考试考试"
					}]

				//				httpSync({
				//					methos: 
				//					//					this.$http.get('http://localhost:3000/api/exams')
				//				})
			},
			/*获取出成绩数据*/
			getExamItemsOrigin() {
				return [{
					"isActive": false,
					"age": 27,
					"name": "张无忌"
					}, {
					"isActive": false,
					"age": 45,
					"name": "孙悟空"
					}, {
					"isActive": false,
					"age": 42,
					"name": "猪八戒"
					}, {
					"isActive": false,
					"age": 15,
					"name": "无极"
					}, {
					"isActive": false,
					"age": 22,
					"name": "狄阿然"
					}, {
					"isActive": false,
					"age": 45,
					"name": "致人"
					}, {
					"isActive": false,
					"age": 45,
					"name": "天二"
					}, {
					"isActive": false,
					"age": 45,
					"name": "舞人生"
					}, {
					"isActive": false,
					"age": 68,
					"name": "子凡"
					}, {
					"isActive": false,
					"age": 78,
					"name": "狂风"
					}, {
					"isActive": false,
					"age": 21,
					"name": "吴迪"
					}, {
					"isActive": false,
					"age": 12,
					"name": "无名"
					}, {
					"isActive": false,
					"age": 28,
					"name": "孔明"
					}, {
					"isActive": false,
					"age": 22,
					"name": "周瑜"
					}, {
					"isActive": false,
					"age": 12,
					"name": "小敏"
					}]

				//				httpSync({
				//					methos: 
				//					//					this.$http.get('http://localhost:3000/api/users')
				//				})
			},
			/*待处理按钮绑定事件*/
			triggersToString(input) {
				if(Array.isArray(input)) {
					return input.join(' + ');
				}
				return input;
			},
			/*toggle的收缩扩展按钮样式1*/
			setClassSwitch() {
				this.examsData.forEach((v, i) => {
					this.classSwitch[0].push("fa fa-plus-square");
				})
			},
			/*toggle的收缩扩展按钮样式2*/
			collapseS(index) {
				this.classSwitch[1].classItem = (this.classSwitch[0][index] === 'fa fa-plus-square') ? 'fa fa-minus-square' : 'fa fa-plus-square';
				this.$set(this.classSwitch[0], index, this.classSwitch[1].classItem);
			},
			/*显示模态框*/
			showAchievementModal(title) {
				this.modalPageTabArr[0].pageTitle = title;
				this.$root.$emit('show::modal', 'achievement');
			},
			/*隐藏模态框*/
			motalclose() {
				this.$root.$emit('hide::modal', 'achievement')
			},
			/*模态框内页面切换*/
			modalPageTab() {
				(this.modalCurrentPage <= 0) && (this.modalCurrentPage = 0);
				(this.modalCurrentPage >= this.modalPageTabArr.length - 1) && (this.modalCurrentPage = this.modalPageTabArr.length - 1);
				this.modalPageTabArr.map(function(item, index) {
					return item.displaySn = 1;
				})
				this.modalPageTabArr[this.modalCurrentPage].displaySn = 0;
				if(this.modalCurrentPage == 1) {
					this.modalPageTabArr[this.modalCurrentPage].pageTitle = "";
					this.modalPageTabArr[this.modalCurrentPage].pageTitle = "第" + this.modalCurrentPage + "页---" + this.modalPageTabArr[0].pageTitle;
				} else if(this.modalCurrentPage == 2) {
					this.modalPageTabArr[this.modalCurrentPage].pageTitle = "";
					this.modalPageTabArr[this.modalCurrentPage].pageTitle = "第" + this.modalCurrentPage + "页---" + this.modalPageTabArr[0].pageTitle;
				}

			},
			/*模态框提交数据*/
			submitExam() {
				console.log(this.items)
			},
			Modalhidden() {}
		},
		computed: {
			modalCurrentPage: {
				get() {
					console.log("1")
				},
				set() {
					console.log("2")
				}
			}
		}
	})
</script>

<style scoped lang="less">
	@import '../assets/less/infoManager.less';
</style>

<style lang="less">
	.table1 {
		font-size: 13px;
		& tr td {
			padding: 3px 5px;
			vertical-align: text-bottom;
		}
	}
</style>


