<template>
	<div id="leftNav">
		<nav id="nav" class="navLeft" v-if="leftNavView">
			<ul class="nav-list" id="sidebar">
				<li>
					<a href="/"><i class="fa fa-home"></i><span class="menu-text">首页</span>
					</a>
				</li>
				<!--<li>
					<a href="/" target="_blank"><i class="fa fa-home"></i><span class="menu-text">后台首页</span>
					</a>
				</li>-->
				<li>
					<a href="#/users" data-toggle="collapse"><i class="fa fa-user"></i><span class="menu-text">用户管理</span><b class="arrow icon-angle-down"></b>
					<!--<router-link to="/users">Go to Foo</router-link>-->
					</a>
					<!--<ul class="submenu collapse" id="users">
						<li>
							<a href="./users.html">用户列表</a> |
							<a href="./userAdd.html">添加</a>
						</li>
					</ul>-->
				</li>
				<li>
					<a href="#/infoManager" data-toggle="collapse"><i class="fa fa-book"></i><span class="menu-text">进行中的考试</span><b class="arrow icon-angle-down"></b>
					</a>
					<!--<ul class="submenu collapse" id="boffin">
						<li>
							<a href="./infoManager.html">进行中的考试</a> |
							<a href="./addBoffins.html">添加</a>
						</li>
					</ul>-->
				</li>
			</ul>
		</nav>
		<div id="switch" @click="leftNav"></div>
	</div>
</template>

<script>
	export default {
		name:"leftNav",
		data(){
			return {
				leftNavView:true
			}
		},
		methods:{
			leftNav(e){
				this.leftNavView=!this.leftNavView;
			}
		}
	}
</script>

<style lang="less" scoped>
	//左侧导航菜单
	.navLeft {
		#users,
		#class,
		#navNewsClass,
		#ads,
		#reports,
		#infoRepertory,
		#boffin {
			border-top: 1px solid #e5e5e5;
		}
		#navNewsClass {
			>li {
				border-bottom: 1px solid #E5E5E5;
				&:last-child {
					border: 1px solid transparent;
				}
			}
		}
		background:#fbfbfb;
		/*display:inline;*/
		/*float: left;*/
/*		width: 200px;*/
		/*height: 100%;*/
		overflow-x: hidden;
		-moz-box-shadow: -5px 0 10px #eee inset;
		/* For Firefox3.6+ */
		-webkit-box-shadow: -5px 0 10px #eee inset;
		/* For Chrome5+, Safari5+ */
		box-shadow: -5px 0 10px #eee inset;
		/* For Latest Opera */
		border-right: 1px solid #ddd;
		.nav-list {
			margin: 0;
			padding: 0;
			list-style: none;
			>li {
				display: block;
				padding: 0;
				margin: 0;
				border: 0;
				border-bottom: 1px solid #e5e5e5;
				position: relative;
				line-height: 35px;
				.submenu li {
					text-indent: 3em;
					a {
						position: relative;
						color: #616161;
						margin: 0;
					}
				}
				a {
					display: block;
					height: 38px;
					line-height: 36px;
					padding: 0 16px 0 7px;
					background-color: #f9f9f9;
					color: #585858;
					text-shadow: none !important;
					text-decoration: none;
					padding-left: 20px;
					>[class*="fa"]:first-child {
						display: inline-block;
						vertical-align: middle;
						min-width: 30px;
						text-align: center;
						font-size: 18px;
						margin-right: 2px;
					}
				}
				[id^="firstId"]>a {
					font-weight: bold;
					color: #585858!important;
					background: #eee;
					border-bottom: 1px solid #eee;
					width: 100%;
					display: block;
					cursor: pointer;
					text-decoration: none;
				}
			}
		}
	}
	
	#switch {
		width: 5px;
		background: #ddd;
		overflow: hidden;
		cursor: pointer;
		color: #585858;
		display:inline;
	}
</style>


