<template>
	<section>
		<b-navbar toggleable type="inverse" variant="success" class="k-bg-success">
			<b-nav-toggle target="nav_collapse"></b-nav-toggle>
			<b-link class="navbar-brand" to="/">
				<span><img src="../assets/images/logo.png"/></span>
			</b-link>
			<b-collapse is-nav id="nav_collapse">
				<ul class="nav-bar-item">
					<li class="active">
						<div class="text"><span class="fa fa-home">&nbsp;</span>首页</div>
					</li>
					<li>
						<div class="text"><span class="fa fa-map">&nbsp;</span>成绩</div>
					</li>
					<li>
						<div class="text"><span class="fa fa-users">&nbsp;</span>学生</div>
					</li>
					<li>
						<div class="text"><span class="fa fa-user">&nbsp;</span>老师</div>
					</li>
				</ul>
				<b-nav is-nav-bar class="ml-auto">
					<b-nav-item>
						<a @click="showModal" class="feedbackText"><span class="fa fa-comments-o">&nbsp;</span>反馈</a>
					</b-nav-item>
					<b-nav-item-dropdown right-alignment>
						<template slot="text">
							<span class="userCenterText">15807550000</span>
						</template>
						<b-dropdown-item to="#">修改信息</b-dropdown-item>
						<b-dropdown-item to="#">重设密码</b-dropdown-item>
						<b-dropdown-item to="#">注销</b-dropdown-item>
					</b-nav-item-dropdown>
				</b-nav>
			</b-collapse>
		</b-navbar>
		<feedbackMotal></feedbackMotal>
	</section>
</template>

<script>
	import feedbackMotal from "@/components/feedbackMotal"
	export default({
		name: "navbar",
		components: {
			feedbackMotal
		},
		data() {
			return {}
		},
		beforeCreate() {

		},
		methods: {
			showModal() {
				this.$root.$emit('show::modal', 'feedbackModal')
			}
		}
	})
</script>

<style lang="less">
	.k-bg-success {
		background: url('../assets/images/headerBg.png') !important;
	}
	
	.navbar-collapse {
		height: 63px;
	}
	
	.feedbackText {
		color: #ffffff;
		text-decoration: none;
	}
	
	.userCenterText {
		color: #ffffff;
		text-decoration: none;
	}
	
	.nav-bar-item {
		li {
			text-align: center;
			list-style: none;
			padding: 10px 10px;
			float: left;
			font-weight: bold;
			margin: 0px 12px;
			margin-top: 3px;
			border-top-left-radius: 5px;
			border-top-right-radius: 5px;
			height: 68px;
			.text {
				color: #B8D2A9;
				font-size: 14px;
				span {
					display: block;
					color: #B8D2A9;
					padding-left: 8px;
					font-size: 30px;
				}
			}
			&.active {
				background: #EAE5CF;
				cursor: pointer;
				span {
					color: #4B380F;
				}
				.text {
					color: #4B380F;
				}
			}
			&:hover {
				background: #EAE5CF;
				cursor: pointer;
				.text {
					color: #4B380F;
					span {
						color: #4B380F;
					}
				}
			}
		}
	}
	.navbar-toggleable-sm .navbar-nav{
		align-items: center;
	}
</style>


