<template>
	<b-modal id="feedbackModal" title="我要反馈" size='lg' :fade="motalFade" :close-on-backdrop="closeOnBackdrop">
		<div id="feedbackBody" class="feedbackBody">
			<p>对海云天说:</p>
			<textarea class="feedbackMsg" name="" rows="" cols=""></textarea>
		</div>
		<span slot="modal-footer">
		<b-button variant='success' size='md' @click="enter">确定</b-button>
		<b-button variant='secondary' size='md' @click="motalclose">取消</b-button>
		</span>
	</b-modal>
</template>
<script>
	export default ({
		name: "feedbackMotal",
		data() {
			return {
				motalFade: true,
				closeOnBackdrop: false
			}
		},
		methods: {
			enter() {
				alert("提交成功!");
			},
			motalclose() {
				this.$root.$emit('hide::modal', 'feedbackModal')
			}
		}
	})
</script>

<style scoped lang="less">
	.row {
		margin-left: 0px!important;
		margin-right: 0px!important;
	}
	.feedbackBody {
		p {
			color: #DDDDDD;
		}
		.feedbackMsg {
			width: 100%;
			height: 180px;
			resize: none;
			border: 1px solid #ccc;
		}
	}
</style>


