<template>
	<div class="users" id="users">
		<div class="justify-content-centermy-1 row">
			<b-form-fieldset horizontal label="" class="col-4" :label-size="3">
				<b-form-input v-model="filter" placeholder="键入关键词查询"></b-form-input>
			</b-form-fieldset>
		</div>
		<b-table striped hover bordered :items="items" :fields="fields" :current-page="currentPage" :per-page="perPage" :filter="filter">
			<template slot="name" scope="item">
				{{item.value}}
			</template>
			<template slot="isActive" scope="item">
				{{item.value?'有效':'无效'}}
			</template>
			<template slot="actions" scope="item">
				<b-btn size="sm" @click="details(item.item),showAlert(item.item),$root.$emit('show::modal','modal1')">修改</b-btn>
				<b-btn size="sm" @click="details(item.item)">删除</b-btn>
			</template>
		</b-table>

		<div class="justify-content-between row my-6">
			<b-form-fieldset horizontal class="col-6" :label-size="0" style="margin-bottom:0px;padding:0px;">每页显示记录数：&nbsp;
				<b-form-select :options="[{text:5,value:5},{text:10,value:10},{text:15,value:15}]" v-model="perPage"></b-form-select>
			</b-form-fieldset>
			<b-pagination size="md" :total-rows="this.items.length" :per-page="perPage" v-model="currentPage" />
		</div>
		<b-modal id="modal1" title="提示" size='lg' :fade="motalFade" :close-on-backdrop="closeOnBackdrop">
			<span slot="modal-footer">
			 	<b-button variant='success' size='md'>确定</b-button>
			 	<b-button variant='secondary' size='md'>取消</b-button>
			 </span>
		</b-modal>
		<!--<div class="justify-content-centermy-8 row">
			<b-form-fieldset horizontal class="col-6" :label-size="0">
				每页显示记录数：&nbsp;
				<b-form-select :options="[{text:5,value:5},{text:10,value:10},{text:15,value:15}]" v-model="perPage"></b-form-select>
			</b-form-fieldset>
		</div>-->
	</div>
</template>

<script>
	export default({
		name: "users",
		data() {
			return {
				items: [{
						"isActive": false,
						"age": 27,
						"name": "张无忌"
					},{
						"isActive": false,
						"age": 45,
						"name": "孙悟空"
					},{
						"isActive": false,
						"age": 42,
						"name": "猪八戒"
					},{
						"isActive": false,
						"age": 15,
						"name": "无极"
					},{
						"isActive": false,
						"age": 22,
						"name": "狄阿然"
					},{
						"isActive": false,
						"age": 45,
						"name": "致人"
					},{
						"isActive": false,
						"age": 45,
						"name": "天二"
					},{
						"isActive": false,
						"age": 45,
						"name": "舞人生"
					},{
						"isActive": false,
						"age": 68,
						"name": "子凡"
					},{
						"isActive": false,
						"age": 78,
						"name": "狂风"
					},{
						"isActive": false,
						"age": 21,
						"name": "吴迪"
					},{
						"isActive": false,
						"age": 12,
						"name": "无名"
					},{
						"isActive": false,
						"age": 28,
						"name": "孔明"
					},{
						"isActive": false,
						"age": 22,
						"name": "周瑜"
					},{
						"isActive": false,
						"age": 12,
						"name": "小敏"
					}],
				fields: {
					name: {
						label: '姓名',
						sortable: true
					},
					age: {
						label: '年龄',
						sortable: true
					},
					isActive: {
						label: '是否有效'
					},
					actions: {
						label: '操作'
					}
				},
				currentPage: 1,
				perPage: 10,
				filter: null,
				//alert
				dismissCountDown: null,
				showDismissibleAlert: false,
				alertTips: null,
				alertPositionTop: null,
				//motal
				motalFade: true,
				closeOnBackdrop: false
			}
		},
		beforeCreate() {
			//			this.$http.get('http://localhost:3000/api/users').then(function(res) {
			//				this.items = res.data.data;
			//			})
		},
		methods: {
			details(item) {

			},
			//alert this.$el.clientWidth
			countDownChanged(dismissCountDown) {
				this.dismissCountDown = dismissCountDown;
			},
			showAlert(item) {
				this.dismissCountDown = 3;
				this.alertTips = item.name;
			}
			//modal
			//			clearName() {
			//				this.name = '';
			//			},
			//			submit(e) {
			//				if(!this.name) {
			//					alert('Please enter your name');
			//					return e.cancel();
			//				}
			//
			//				this.names.push(this.name);
			//				this.name = '';
			//			}
		}
	})
</script>

<style scoped>
	
	.row {
		margin-left: 0px!important;
		margin-right: 0px!important;
	}
	.justify-content-centermy-1 .row{
		margin-left: -25px!important;
	}
</style>


