<template>
	<div id="container" class="container-full">
		<header>
			<navbar></navbar>
		</header>
		<section class="container">
			<leftNav class="left"></leftNav>
			<AppRightContainer class="right background"></AppRightContainer>
		</section>
		<!--<section id="footer">
			<footer>copyright 2017</footer>
		</section>-->
	</div>
</template>

<script>
	import navbar from "@/components/navbar"
	import leftNav from "@/components/leftNav"
	import AppRightContainer from "./../AppRightContainer"

	export default {
		name: "index",
		components: {
			navbar,
			leftNav,
			AppRightContainer,
		},
		beforeCreate(){
//			this.$router.push("/infoManager")
		}
		
	}
</script>

<style scoped>
	h1,
	h2 {
		font-weight: normal;
	}
	
	ul {
		list-style-type: none;
		padding: 0;
	}
	
	li {
		display: inline-block;
		margin: 0 10px;
	}
	
	a {
		color: #42b983;
	}
	
	#footer:after {
		content: '';
	}
	
	.left {
		display: flex;
		flex: 0 0 auto;
	}
	
	.right {
		/*overflow: hidden;*/
		/*margin-left: 205px;*/
		padding: 10px;
		width: 100%;
		/*height: 100%;*/
		overflow-y: auto;
	}
	.background {
		/*background:url(../assets/images/mainBg.png);*/
		background: #FFFFFF;
	}
	.container{
		display: flex;
		align-content: space-between;
		justify-content: space-between;
		height: calc(100vh - 79px);
	}
	#container{
		height:auto;
	}
	
</style>


